.o-coupon {
  position: relative;
  border-radius: 1.875rem;
  overflow: hidden;
  text-align: center;
  max-width: 23.75rem;
  margin: 0 auto;
  width: 100%;

  &__pretitle {
    margin: 0;
  }

  &__code {
    margin: 0 0 0.4375rem;

    &.is-desktop {
      display: none;
    }
  }

  &.is-redeemed {
    background-color: $s-color--background;
    color: $s-color--font-focus;
    padding: 1.75rem 1.75rem 1.875rem;
  }

  &.is-available {
    background: transparent;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;

    &.is-flipped {
      .o-coupon__front-face {
        opacity: 0;
        z-index: $s-z--reset;
      }

      .o-coupon__back-face {
        opacity: 1;
        z-index: $s-z--text-overlay;
      }
    }

    .o-coupon {
      &__front-face {
        padding: 1.75rem 1.75rem 1.875rem;
        background: $s-color--background-impact;
        transition: opacity 0.2s;
        grid-row: 1/1;
        grid-column: 1/1;
        z-index: $s-z--text-overlay;
        color: $s-color--font-invert;
      }

      &__back-face {
        padding: 1.75rem 1.75rem 1.875rem;
        transition: opacity 0.2s;
        background: $s-color--background;
        grid-row: 1/1;
        grid-column: 1/1;
        opacity: 0;
        z-index: $s-z--reset;
        color: $s-color--font-focus;
      }


      &__front-btn {
        background: $s-color--btn-background-alt;
        color: $s-color--font-focus;
      }
    }
  }

  &.is-unavailable {
    background: $s-color--background-impact;
    color: $s-color--font-invert;
    padding: 1.75rem 1.75rem 1.875rem;
    opacity: 0.25;

    &:nth-child(1) {
      opacity: 0.5;
    }

    .o-coupon {
      &__pretitle {
        color: $s-color--font-invert;
      }

      &__btn {
        background: $s-color--btn-background-alt;
        color: $s-color--font-focus;
      }
    }
  }

  &__name {
    margin-bottom:0.625rem;
  }


  @include t-media-query($s-small-up) {
    text-align: left;
    max-width: initial;
  }

  @include t-media-query($s-medium-up) {
    text-align: left;
    max-width: initial;

    &.is-unavailable {
      padding: 3.125rem 2.6875rem 3.75rem;
    }

    &.is-redeemed {
      padding: 3.125rem 2.6875rem 3.75rem;

      .o-coupon__btn {
        background: transparent;
        color: $s-color--font-focus;
        border: 1px solid currentColor;
        cursor: initial;
      }
    }

    &.is-available {
      background: transparent;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr;

      &.is-flipped {
        .o-coupon__front-face {
          opacity: 0;
          z-index: $s-z--reset;
        }

        .o-coupon__back-face {
          opacity: 1;
          z-index: $s-z--text-overlay;
        }
      }

      .o-coupon {
        &__front-face {
          padding: 3.125rem 2.6875rem 3.75rem;
        }

        &__back-face {
          padding: 3.125rem 2.6875rem 3.75rem;
        }


        &__front-btn {
          background: $s-color--btn-background-alt;
          color: $s-color--font-focus;
        }
      }
    }

    &__name {
      margin-bottom: 1.25rem;
    }

    &.is-available.is-flipped {
      text-align: center;

      &::after {
        content: none;
      }
    }

    &::after {
      position: absolute;
      background: url(../../assets/svg.coupon-bird-dark.svg) no-repeat center center;
      content: "";
      display: block;
      top: -1.3125rem;
      right: -9.625rem;
      width: 22.625rem;
      height: 22.125rem;
      z-index: $s-z--illustration-overlay;
    }

    &.is-redeemed::after {
      background: url(../../assets/svg.coupon-bird.svg) no-repeat center center;
    }

    &__pretitle {
      margin: 0 0 0.6875rem;
    }
  }

  @include t-media-query($s-large-up) {
    &::after {
      right: -4.625rem;
    }
  }
}