.o-icon-blocks {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 2.375rem;
  row-gap: 2rem;
  column-gap: 0.25rem;

  &__block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__icon-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__icon {
    width: 100%;
    max-height: 3.5rem;
  }

  &__content {
    text-align: center;
    margin-top: 0.4375rem;
  }

  &__line-1 {
    margin-bottom: 0.25rem;
  }
  &__line-2 {
    color: $s-color--font-focus;
    margin-bottom: 0;
  }

  @include t-media-query($s-small-up) {
    grid-template-columns: repeat(3, 1fr);
  }
  
  @include t-media-query($s-medium-up) {
    grid-template-columns: repeat(4, 1fr);
    row-gap: 3.875rem;
    margin-top: 5.125rem;
    column-gap: 1rem;

    &__icon {
      max-height: 4rem;
    }

    &__content {
      margin-top: 0.9375rem;
    }
  }

  @include t-media-query($s-large-up) {
    grid-template-columns: repeat(4, 1fr);
    row-gap: 3.875rem;
    column-gap: 2.5625rem;
  }
}
