.c-rewards-details {
  background: $s-color--background-subdued;

  &__inner {
    padding: 2.3125rem 0.9375rem 4.4375rem;
  }

  &__tabs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5625rem;
  }

  &__tab {
    color: rgba($s-color--font, .4);
    cursor: pointer;
    transition: all .2s;

    &.is-active {
      color: $s-color--font;
    }
  }

  &__content-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    margin-top: 2.625rem;
  }

  .o-icon-blocks {
    row-gap: 1.875rem;

    &__line-1 {
      margin-bottom: 0.1875rem;
    }
  }

  &__content {
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s;
    grid-row: 1/-1;
    grid-column: 1/-1;
    height: 0;

    &.is-visible {
      opacity: 1;
      visibility: visible;
      height: auto;
    }
  }

  &__rewards-subtitle{
    width: 68%;
    margin: 0 auto;
    text-align: center;
  }

  &__rewards-wrapper{
    margin-top: 3rem;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2rem;
  }

  &__activity-wrapper{
    max-width: 25.625rem;
    margin: 0 auto;
  }

  &__activity{
    display: grid;
    grid-template-columns: 1fr max-content;
    row-gap: 0.5rem;
    color: rgba($s-color--font, .6);
    padding: 2rem 0;
    border-bottom: 1px solid rgba($s-color--border-focus, .2);

    p{
      margin: 0;
    }
  }

  &__activity-name{
    color:$s-color--font;
  }

  &__activity-message{
    text-align: center;
  }

  &__help-wrapper{
    text-align: center;
    max-width: 43.75rem;
    margin: 0 auto;
   }


  @include t-media-query($s-small-up) {
    &__rewards-wrapper{
      grid-template-columns: 1fr 1fr;
    }

    &__help-wrapper{
     padding: 0 2.75rem;
    }

  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 5.625rem 10% 18.8125rem
    }

    &__tabs {
      flex-direction: row;
      gap: 3.875rem;
    }

    .o-icon-blocks {
      row-gap: 4.0625rem;
      margin-top: 2.8125rem;
  
      &__line-1 {
        margin-bottom: 0.4375rem
      }
    }

    &__rewards-wrapper{
      grid-gap: 2.5rem;
      margin: 4.375rem auto 0;
      max-width: 72.5rem;
    }

    &__content-wrapper{
      max-width: 72.5rem;
      margin: 3rem auto 0;
    }

  }
}